import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login/Login'
import queue from '@/components/login/queue'
 
Vue.use(Router)
 
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/queue',
      name: 'queue',
      component: queue
    }
  ]
})